@import url('https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300;400;700&display=swap')

/* scrollbars */

::webkit-scrollbar {
  width: 5px;
  height: 6px;
}

::webkit-scrollbar-track{
  box-shadow: inset 0 0 5px #a5aaad;
  border-radius: 10px;
}

::webkit-scrollbar-thumb{
  background: #3ea175;
  border-radius: 10px;
}

::webkit-scrollbar-thumb:hover{
  background: #a5aaad;
}

/* geral */

*{
  margin: 0;
  padding: 0;
}

body{
  box-sizing: border-box;
  font-family: 'Open Sans Condensed', sans-serif;
}

.text-primary-p{
  color: #a5aaad;
  font-size: 14px;
  font-weight: 700;
}

.font-bold{
  font-weight: 700;
}

.text-title{
  color: #fff;
}

.text-lightblue{
  color: #469cac;
}

.text-white{
  color: #fff;
}

.text-pink{
  color: #fcc5f3;
}

.text-red{
  color: #cc3d38;
}

.text-yellow{
  color: #a98921;
}

.text-green{
  color: #3b9668;
}

.container{
  display: grid;
  height: 100vh;
  grid-template-columns: 0.8fr repeat(3, 1fr);
  grid-template-rows: 0fr 3fr;
  grid-template-areas:
  'sidebar nav nav nav'
  'sidebar main main main';
}

@media only screen and (max-width: 978px){
  .container{
    grid-template-columns: 1fr;
    grid-template-rows: 0.2fr 3fr;
    grid-template-areas: "nav" "main";
  }
}
#sidebar {
    background-image: linear-gradient(#c153ec 40%, #da4fd1 90%);
    grid-area: sidebar;
    overflow-y: auto;
    padding: 20px;
    transition: all 0.5s;
}

.sidebar__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    margin-bottom: 30px;
}

.sidebar__img {
    display: flex;
    align-items: center;
}

.sidebar__title > div > img {
    width: 55px;
    object-fit: contain;
    border-radius: 50%;
}

.sidebar__title > div > h1 {
    font-size: 18px;
    display: inline;
    margin-left: 15px;
}

.sidebar__title > i {
    font-size: 18px;
    display: none;
}

.sidebar__menu > h2 {
    color: #fff;
    font-size: 16px;
    margin-top: 15px;
    margin-bottom: 5px;
    padding: 0 10px;
    font-weight: 700;
}

.sidebar__link {
    color: #fff;
    padding: 10px;
    border-radius: 3px;
    margin-bottom: 5px;
}

.active_menu_link {
    color: #fff;
}

.active_menu_link a {
    color: #fff !important;
}

.sidebar__link > a {
    text-decoration: none;
    color: #fff;
    font-weight: 700;
}

.sidebar__link a:focus {
    padding: 7px;
    background: #a04bc2;
    border-radius: 3px;
}

.sidebar__link > i {
    margin-right: 10px;
    font-size: 18px;
}

.sidebar-responsive {
    display: inline !important;
    z-index: 9999 !important;
    left: 0 !important;
    position: absolute;
}

@media only screen and (max-width: 978px) {
    #sidebar {
        display: none;
    }
    .sidebar__title > i {
        display: inline;
    }
}
main {
    background: #1e1e2e;
    grid-area: main;
    overflow-y: auto;
}

.main__container {
    padding: 20px 35px;
}

.main__title {
    display: flex;
    align-items: center;
}

.main__title > img {
    max-height: 100px;
    object-fit: contain;
    margin-right: 20px;
}

.main__greeting > h1 {
    font-size: 24px;
    color: #fff;
    margin-bottom: 5px;
}

.main__greeting > p {
    font-size: 14px;
    font-weight: 700;
    color: #ce75c0;
}

.main__cards{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 30px;
    gap: 30px;
    margin: 20px 0;
}

.card {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    height: 70px;
    padding: 25px;
    border-radius: 5px;
    background-color: #27293d;
}

.card_inner{
    align-items: center;
    justify-content: space-between;
    padding-right: 25px;
}

.card_inner > p {
    font-size: 15px;
    color: #fff;
}

.card_inner > span {
    font-size: 25px;
    color: #c652e7;
}

.charts{
    padding: 25px;
    border-radius: 5px;
    background: #27293d;
}

.charts__title{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.charts__title > div > h1 {
    font-size: 24px;
    color: #fff;
    margin-bottom: 5px;
}

.charts__title > div > p {
    font-size: 14px;
    font-weight: 700;
    color: #848488;
}

.charts__title > i {
    color: #FFF;
    font-size: 20px;
    background: #c652e7;
    border-radius: 200px 0 px 200px 200px;
    -moz-border-radius: 200px 0px 200px 200px;
    -webkit-border-radius: 200px 0px 200px 200px;
    border: 0px solid #1e1e2e;
    padding: 15px;
}

@media only screen and (max-width: 855px){
    .main__cards{
        grid-template-columns: 1fr;
        grid-gap: 10px;
        gap: 10px;
        margin-bottom: 0;
    }

    .charts{
        grid-template-columns: 1fr;
        margin-top: 30px;
        justify-content: center;
    }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.formlancamento{
    background: #1e1e2e;
    grid-area: main;
    overflow-y: auto;
    padding: 20px 35px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    color: #848488;
}

button, input, body {
    font-family: 'Open Sans Condensed', sans-serif;
    font-weight: 700;
}

.flanc__container{
    width: 450px; 
    height: 550px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    background: #27293d;
}

.flanc__title{
    font-size: 14px;
    padding: 10px;
    color: #fff;
    margin-bottom: 20px;
}

.flanc__form > label, .form_group > label{
    font-size: 14px;
    font-weight: 700;
    color: #ce75c0;
    margin-bottom: 10px;
}

.flanc__radio{
    margin-top: 15px;
}

.rad-label {
    display: flex;
    align-items: center;
  
    border-radius: 5px;
    padding: 10px 10px;
  
    cursor: pointer;
    transition: .3s;
}

.rad-label:hover,
.rad-label:focus-within {
  background: rgba(187, 89, 190, 0.14);
}

.rad-input {
    position: absolute;
    left: 0;
    top: 0;
    width: 1px;
    height: 1px;
    opacity: 0;
    z-index: -1;
}

.rad-design {
    width: 14px;
    height: 14px;
    border-radius: 100px;
  
    background: linear-gradient(to right bottom, hsl(276, 65%, 54%), hsl(303, 63%, 67%));
    position: relative;
}

.rad-design::before {
    content: '';
  
    display: inline-block;
    width: inherit;
    height: inherit;
    border-radius: inherit;
  
    background: hsl(297, 75%, 95%);
    transform: scale(1.1);
    transition: .3s;
}

.rad-input:checked+.rad-design::before {
    transform: scale(0);
}

.rad-text {
    color: #848488;
    margin-left: 10px;
    font-size: 14px;
    font-weight: 700;
    transition: .3s;
}
  
.rad-input:checked~.rad-text {
    color: #fcc5f3;
}

.form_group{
    margin-bottom: 15px;
}

.form_group_control {
    width: 100%;
    padding: 10px 0;
    background: none;
    border: none;
    border-bottom: 1px solid #848488;
    color: #fff;
    font-size: 14px;
    outline: none;
    transition: border-color .2s;
}

.form_group_control:-ms-input-placeholder{
    color: #ce75c0;
}

.form_group_control::placeholder{
    color: #ce75c0;
}

.form_group_control:focus{
    border-bottom-color: #ce75c0;
}

.form_group.button{
    padding-top: 10px;
    text-align: right;
}

.form_button {
    background: none;
    border: none;
    color: #fff;
    font-size: 14px;
    cursor: pointer;
    outline: none;
  }
  
  .form_button:hover {
    color: #ce75c0;
  }
.lanc{
